import goBack from '@/components/back.vue';
export default {
	data() {
		return {
			ruleUrl: getApp().globalData.imgUrl + '/privacy.png',
			safeAreaTop:getApp().globalData.safeAreaTop?getApp().globalData.safeAreaTop+'px':'64rpx'
		}
	},
	onLoad(options) {
		uni.showLoading({
			title: ''
		})
		if (options.ruleUrl) {
			this.ruleUrl = options.ruleUrl;
		}
	},
	methods: {
		imgLoad(e) {
			if (e.detail) {
				uni.hideLoading()
			}
		}
	},
	components: {
		goBack
	}
}
